<template>
  <v-app id="app">
    <v-main>
      <router-view />
      <div class="text-center">
        <v-overlay :value="overlay">
          <v-progress-circular indeterminate size="64"></v-progress-circular>
          {{ errorStr }}
        </v-overlay>
        <!-- <footerF /> -->

        <login v-model="logearme" />
      </div>



      <v-dialog v-model="isLocation" persistent max-width="290">
        <v-card class="text-center">
          <v-card-title class="text-h6 text-center">
            <div style="width: 100%;"> <v-icon style="zoom:4; color:red">mdi-map-marker-off</v-icon> </div>
            ¡Ubicación Desconectada!

          </v-card-title>
          <v-card-text class="text-justify">Detectamos que <b>NO</b> tienes activa tu <b>Ubicación</b> . Activala y
            recarga la página para tener la mejor experiencia.</v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn color="green darken-1" text @click="isLocation = false">
              Ok
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-main>

  </v-app>
</template>

<script>
//import axios from "axios";
import device from './utils/detecte-device'
import genRandonString from '@/controller/generateCode'
import * as VueGoogleMaps from 'vue2-google-maps'
import store from './store'
import api from "./models/controller";
import { mdiExitRun } from "@mdi/js";
//import footerF from "@/components/Footer/Footer.vue";
import login from "@/components/Login/Login.vue";
import { mdiArrowUpBoldCircle } from "@mdi/js";
import { mdiStoreCheck } from "@mdi/js";
import { mdiAccountCash } from "@mdi/js";
import { mdiLogin } from "@mdi/js";
import { mdiFolder } from "@mdi/js";
import { mdiCardAccountDetails } from "@mdi/js";



export default {
  components: { login /*footerF*/ },
  name: "App",
  data() {
    return {
      noti:true,
      dirreccion: null,
      question: "",
      overlay: false,
      logearme: null,
      user: {},
      isLocation: false,
      isLogin: false,
      drawer: false,
      tab: null,
      errorStr: '',
      appdrawer: {
        mdiFolder,
        mdiExitRun,
        mdiArrowUpBoldCircle,
        mdiStoreCheck,
        mdiAccountCash,
        mdiLogin,
        mdiCardAccountDetails,
      },
      items: ["Subir", "shopping", "videos", "images", "news"],
      icons: ["mdi-facebook", "mdi-twitter", "mdi-linkedin", "mdi-instagram"],
    };
  },


  methods: {


    setVisita: function () {
      navigator.geolocation.getCurrentPosition(
        position => {
          this.$gmapApiPromiseLazy().then(() => {
            var geocoder = new this.google.maps.Geocoder();
            if (geocoder) {
              var latlng = { lat: parseFloat(position.coords.latitude), lng: parseFloat(position.coords.longitude) };
              geocoder.geocode({ 'location': latlng }, (results, status) => {
                if (status === 'OK') {
                  if (results[0]) {
                    var terminal = '';
                    genRandonString().then((re) => {
                      if (localStorage.terminal) {
                        terminal = localStorage.terminal
                      } else {
                        localStorage.terminal = re;
                        terminal = re;
                      }
                      var modelo = '';
                      var marca = '';
                      var so = '';
                      var navegador = '';
                      var tipoDis = '';

                      modelo = device.model;
                      marca = device.brand;
                      navegador = "Nav - " + device.browser_name + "    ver - " + device.browser_version;
                      if (device.isMobile) {
                        tipoDis = 'Movil'
                      }

                      if (device.isAndroid) {
                        tipoDis = 'Android'
                      }

                      if (device.isIOS) {
                        tipoDis = 'iOS'
                      }

                      if (device.isChromeOs) {
                        tipoDis = 'isChromeOS'
                      }
                      /*TIPO DE NAVEGADOR/*/

                      if (device.os.android) {
                        so = 'Android'
                      }

                      if (device.os.windows) {
                        so = 'Windows'
                      }

                      if (device.os.mac) {
                        so = 'Mac'
                      }
                      var fecha = new Date();

                      var data = {
                        terminal: terminal,
                        marca: marca,
                        modelo: modelo,
                        so: so,
                        type: device.type,
                        browser_engine: device.browser_engine,
                        navegador: navegador,
                        tipoDispo: tipoDis,
                        visita: 1,
                        fecha: fecha,
                        fecha_ulvista: fecha,
                        direccion: results[0].formatted_address,
                        comentario: 'ninguno',
                        lat: position.coords.latitude,
                        lng: position.coords.longitude
                      }
                      api.createVistaPagina(data)
                    })

                  }
                }
              })
            }
          })
        },
        error => {
          console.log(error)
        },
      )

    },

    codeAddress(lat, lng) {
      this.$gmapApiPromiseLazy().then(() => {
        var geocoder = new this.google.maps.Geocoder();
        if (geocoder) {
          var latlng = { lat: parseFloat(lat), lng: parseFloat(lng) };
          geocoder.geocode({ 'location': latlng }, (results, status) => {
            if (status === 'OK') {
              if (results[0]) {
                this.direccion = results[0].formatted_address



              }
            }
          })
        }
      })
    },

    activeLocation() {
      this.isLocation = false;
    },
    paginar(pagina) {
      //`/perfil/${user.id_user}/ventas`
      this.$router
        .push(`/perfil/${this.user.id_user}/${pagina}`)
        .catch(() => { });
    },
    salir() {
      window.localStorage.removeItem("user");
      this.$router.go();
    },
    islogen() {
      if (window.localStorage.getItem("user")) {
        this.isLogin = true;
        var user = JSON.parse(localStorage.getItem("user"));
        this.user = user.user;
      } else {
        this.isLogin = false;
        console.log("NO LOGEADO EL USUARIO");
      }
    },
    async getGeolocationInformation() {
      /* navigator.geolocation.getCurrentPosition(
         position => {
 
         },
         error => {
           this.isLocation=true;
           
         },
       )*/
    }
  },
  beforeCreate() {
    this.overlay = true;
    try {
      this.$OneSignal.showSlidedownPrompt();

    } catch (error) {
      console.log(" Code ", error)
    }

  },
  computed: {
    google: VueGoogleMaps.gmapApi
  },
  mounted() {
    /*  if (!("geolocation" in navigator)) {
        return alert("Activa tu Ubicación para poder tener la mejor experiencia");
      } */
    this.setVisita();
  },



  created() {



    if (navigator.geolocation) {
      //navigator.geolocalización.getCurrentPosition(getPosition);
      //return alert("Tu navegador no soporta el acceso a la ubicación. Intenta con otro");

      this.overlay = true;
      if (store.getters.isLoggedIn) {
        var data = store.getters.getUser;
        var user = {
          user: data
        }
        localStorage.setItem("user", JSON.stringify(user));
        api.getVeryEmail(data.id_user).then((result) => {
          var very = result.data;
          var isVery = very.result;
          if (isVery.verify_email) {
            // this.user= user.user;
            this.$OneSignal.getUserId().then((result) => {
              api.updateOnesignal(data.id_user, result)
            })
            api.obtenerIdOnesignal(data.id_user);
            api.getUser(data.id_user).then((result) => {
              this.user = result.data[0];
              localStorage.setItem("user", JSON.stringify(this.user));
            });
          } else {
            const path = `/veryEmail`
            if (this.$route.path !== path) this.$router.push(path)
          }
        })
      } else {
        this.isLogin = false;
      }
    } else {
      console.log("sin conexion")
    }


  },
  watch: {
    overlay(val) {
      val &&
        setTimeout(() => {
          this.overlay = false;
        }, 3000);
    },
  },
};
</script>

<style scoped>
@font-face {font-family: "Estilo1";src: local("Helvetica Neue Bold"),local("HelveticaNeue-Bold"),url(./assets/fonts/tangosans/TangoSans.ttf);font-weight: bold;}




