<template>
   <div class="text-center">
  <v-dialog v-model="show" persistent max-width="350px">
    <v-card class="text-center rounded-lg">
      <v-card-text>
        <v-container class="text-center" >
          <v-row no-gutters>
            <v-col cols="12">
              <v-icon class="icon-close" @click="show = false">mdi-close</v-icon>
              <v-avatar class="mt-2" color="green" size="100">
                <v-img width="170" src="../../assets/logo.png" style="position: relative;">
                  <v-progress-circular v-if="activeProgress == true" size="97" width="4" class="progres-circular"
                    indeterminate color="amber"></v-progress-circular>
                </v-img>
              </v-avatar>
              <p class="font-weight-medium subtitle-1 mt-2">INICIAR SESIÓN</p>
              <v-alert type="error" v-if="error" style="padding-left:-10px">
                {{ mensajeError }}
              </v-alert>
            </v-col>
            <v-col cols="12">

              <v-form ref="form">
                <div class="centrarfrom">
                  <v-text-field prepend-inner-icon="mdi-email-outline" rounded v-model="email" :rules="[rules.email]"
                    filled color="green" label="Correo electrónico" type="email"></v-text-field>
                  <v-text-field class="mt-0" prepend-inner-icon="mdi-key-outline" rounded v-model="password" filled
                    color="green" label="Contraseña" 
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'" :type="show1 ? 'text' : 'password'"
                    @click:append="show1 = !show1"></v-text-field>
                  <v-btn rounded large dark color="green" block @click="login">Entrar </v-btn>
                  <p class="contrasenia">
                    ¿Olvidaste tu contraseña?
                    <span class="recuperar" @click="restorePassword()"> Recuperar</span>
                  </p>
                  <p class="btn-crear">
                    ¿Aún no posees cuenta?
                    <a class="btn-registrar" href="/registro">REGISTRARME</a>
                  </p>
                </div>
              </v-form>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>
  </v-dialog>
</div>
</template>
<script>
//import axios from "axios";
import api from "../../models/controller";
import store from '../../store'
//import AuthService./ from '@/services/AuthService.js';
//alert("La resolución de tu pantalla es: " + screen.width + " x " + screen.height)
export default {
  props: {
    value: Boolean,
    product: null,
  },

  data() {
    return {
      error: false,
      activeProgress: false,
      mensajeError: null,
      show1: false,
      dialog: false,
      email: '',
      password: '',
      userSaved: false,
      sending: false,
      rules: {
        email: (v) =>
          !!(v || "").match(/@/) || "Por favor ingresa un correo válido",
        length: (len) => (v) =>
          (v || "").length >= len ||
          `Longitud de carácter no válida, obligatorio ${len}`,
        password: (v) =>
          !!(v || "").match(
            /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*(_|[^\w])).+$/
          ) ||
          "La contraseña debe contener una letra mayúscula, un carácter numérico y un carácter especial",
        required: (v) => !!v || "Este campo es obligatorio",
      },
    };
  },
  methods: {
    login: async function () {
      this.activeProgress = true;
      this.sending = true;
      api.login(this.email, this.password).then((datos) => {
        var data = datos.data;

        if (data == 'Error, No Existe correo') {
          this.activeProgress = false;
          this.mensajeError = 'Correo no ha sido registrado.';
          this.error = true;
          setTimeout(() => {
            this.error = false;
            this.mensajeError = null;
          }, 3000)
          window.localStorage.removeItem('user');

        } else if (data == 'Error, Usuario o contrasenia incorrecta') {
          this.activeProgress = false;
          this.mensajeError = 'Correo o contraseña no válidos.';
          this.error = true;
          setTimeout(() => {
            this.error = false;
            this.mensajeError = null;
          }, 3000)
          window.localStorage.removeItem('user');

        } else {
          const user = data.user;
          const token = data.token;
          store.dispatch('login', { token, user });
          window.localStorage.setItem("user", JSON.stringify(datos.data));
          window.localStorage.email = this.email;
          window.localStorage.pass = this.password;
          //var da = localStorage.getItem("user");
          this.userSaved = true;
          this.sending = false;
          this.show = false;
          this.reset();
          this.clearForm();
          this.error = false;

          this.$router.go();
        }

      });
    },

    restorePassword() {
      this.$router.push("/restorePasword").catch(() => { });
    },
    reset() {
      this.$refs.form.reset();
    },
    clearForm() {
      this.email = '';
      this.password = '';
    },
  },
  mounted() {
    if (!localStorage.email) {
      this.email = window.localStorage.email;
    } else {
      this.email = ''
    }
    if (!localStorage.pass) {
      this.password = window.localStorage.pass;
    } else {
      this.password = ''
    }
  },
  computed: {
    show: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },

  created() {
  }
};
</script>

<style  lang='scss' scoped>


.centrarfrom {
  width: 100%;
  height: 300px;
}

.titulo {
  font-weight: bolder;
  font-size: 18pt;
  color: #0eb308;
  text-align: center;
  margin-bottom: 1.5rem;
  //text-shadow: 1px 1px 6px black;
}

.card-toolbar {
  background: #0eb308 !important;
}

.btn-entrar {
  margin-top: 10px;
  width: 100%;
  background: #0eb308;
  font-size: 12pt;
}

.contrasenia {
  margin-top: 15px;
  font-size: 10pt;
  font-weight: bolder;
}

.recuperar {
  margin-top: 10px;
  color: rgb(11, 11, 247);
  font-weight: bolder;
  text-decoration: underline;
}

.btn-crear {
  font-size: 12pt;
  font-weight: bolder;
  text-align: center;

  .btn-registrar {
    font-weight: bolder;
    color: rgb(11, 11, 247);
    font-size: 12pt;
    text-decoration: underline;
    //text-shadow: 1px 1px 1px #363636;
    margin-bottom: 15px;
  }
}

a {
  color: rgb(11, 11, 247) !important;
}

.form-submit {
  margin-top: 5px;
  background: #15d826;
  border: none;
  width: 100%;
  border-radius: 8px;
  color: white;
  padding: 1rem 0;
  font-weight: bolder;
  font-size: 18pt;
  cursor: pointer;
  transition: background 0.2s;

  &:hover {
    background: #02aa43;
  }
}

.icon-close {
  position: absolute;
  top: 15px;
  right: 5px;
  color: red !important;
  font-weight: bolder;
}

.progres-circular {
  position: absolute;
  top: 2px;
  left: 2px;
}
</style>